<template>
  <div class="main-box">
    <div class="title" v-text="$t('menu.portfolio')"></div>
    <div class="line-title"></div>
    <div class="list">
      <!--menu-->
      <div class="head-menu">
        <v-menu
            :close-on-content-click="false"
            :nudge-width="200"
            offset-y
            style="display: inline-block;">
          <template v-slot:activator="{ on, attrs }">
            <v-btn
                id="btn-filter"
                style="text-transform: none;"
                color="var(--ad-blue)"
                dark
                v-bind="attrs"
                v-on="on">
              Filter
              <v-icon
                  light
                  right
                  style="font-size: 16px; padding-top: 5px; margin-left: 18px;"
              >
                fas fa-chevron-down
              </v-icon>
            </v-btn>
          </template>
          <v-card>
            <v-list class="p-3">
              <div class="form-check" v-for="(item, index) in category" :key="'filter-'+index">
                <input class="form-check-input" type="checkbox" :value="item" :id="'filter-' + index" v-model="filter" @input="filable()">
                <label class="form-check-label" :for="'filter-' + index">
                  {{item}}
                </label>
              </div>
            </v-list>
            <v-card-actions>
              <v-btn text @click="filter = []; filable();"  style="width: 100%; background: rgb(165 184 203 / 31%);">
                Clear
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-menu>
        <div class="form-group col-xl-4" id="search-box" style="display: inline-block; height: 36px; float: right; position: relative">
          <input type="text" class="form-control" id="search" :placeholder="$t('search')" style="height: 36px; padding-right: 35px" autocomplete="off" v-model="search" @input="filable()">
          <v-icon style="font-size: 16px; position: absolute; right: 12px; top: 12px;">
            fas fa-search
          </v-icon>
        </div>
      </div>
      <!--list item-->
      <div class="row row-cols-3 m-auto" >
        <div class="col text-center" v-show="loading">
          <div class="mini-box">
            <div class="image animated-background"></div>
          </div>
        </div>

        <div class="col text-center item-list" v-for="(item, index) in portfolioTemps" v-bind:key="index">
            <div class="mini-box" @click="openDetail(item.id)">
              <div class="image animated-background">
                <img :src="item.image" :alt="item.title" @load="onImgLoad" @error="onImgError" style="object-fit: cover">
              </div>
              <span class="title-progress" v-text="item.category"></span>
            </div>
            <span class="text" v-html="item.title"></span>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Portfolio",
  watch: {
    // eslint-disable-next-line no-unused-vars
    '$i18n.locale': function(newVal, oldVal) {
      this.portfolio = {};
      this.portfolioTemps = {};
      this.category = {};
      this.filter = [];
      this.search = '';
      this.loading = true;
      this.getPortfolio();
    }
  },
  beforeMount() {
    this.getPortfolio();
    this.$parent.$data.showMenu = true;
  },
  data: () => ({
    portfolio: {},
    portfolioTemps: {},
    loading: true,
    category: {},
    filter: [],
    search: ''
  }),
  methods:{
    openDetail:function (id){
      this.$router.push(`/detail-portfolio/${id}`)
    },
    onImgLoad:function(e) {
      e.path[1].classList.remove("animated-background");
    },
    onImgError:function(e) {
      e.target.src = '/image/no-image-white.png';
    },
    filable:function (){
      let self = this;
      setTimeout(function (){
        if(self.filter.length > 0){
          self.portfolioTemps = JSON.parse(JSON.stringify(self.portfolio)).filter(skill => {
            return self.filter.includes(skill.category) && skill.title.toLowerCase().includes(self.search.toLowerCase());
          });
        }else{
          self.portfolioTemps = JSON.parse(JSON.stringify(self.portfolio)).filter(skill => skill.title.toLowerCase().includes(self.search.toLowerCase()));
        }
        setTimeout(function () {
          let list = document.querySelectorAll(".list .item-list");
          list.forEach((v, k) => {
            setTimeout(() => {
              v.classList.add("show")
            }, k * 50);
          })
        },100);
      }, 100);
    },
    getPortfolio:function (){
      let activeLang = localStorage.getItem("user-lang");
      if(activeLang == null){
        activeLang = "en";
      }
      this.axios.get(`portfolio?lang=${activeLang}&token=${this.$parent.getToken()}`).then((response) => {
        this.loading = false;
        this.category = response.data.category;
        this.portfolio = response.data.data;
        this.portfolioTemps = response.data.data;
        setTimeout(function (){
          let list = document.querySelectorAll(".item-list");
          list.forEach((v, k)=>{
            setTimeout(()=>{
              v.classList.add("show")
            }, k * 100);
          })

        }, 200);
      }).catch((error) =>{
        console.log(error);
      })
    }
  }
}
</script>

<style scoped>
  .mini-box{
    height: 140px;
    width: 100%;
    overflow: hidden;
    background-color: white;
    box-shadow: 0.1px 2px 8px rgba(0, 0, 0, 0.20);
    display: inline-block;
    border-radius: 10px;
    margin-top: 10px;
    position: relative;
    cursor: pointer;
  }

  .list{
    overflow-y: scroll;
  }

  .image{
    overflow: hidden;
    height: 140px;
  }

  .image img{
    width: 100%;
    height: 140px;
  }

  .text{
    font-weight: bold;
    margin-bottom: 10px;
  }

  .show {
    display: inline-block !important;
  }

  .head-menu{
    padding: 0px 18px 18px 18px;
  }

  .title-progress{
    padding: 6px;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: bold;
    background: #055DE1;
    position: absolute;
    top: 0px;
    color: white;
    right: 0px;
  }

  @media screen and (max-width: 960px) {
    .head-menu .v-menu__content{
      min-width: initial!important;
    }
    #btn-filter{
      width: 100%;
      margin-bottom: 10px;
    }
    #search-box{
      width: 100%!important;
      margin-bottom: 20px;
    }
    .line-title{
      width: 108px;
    }

    .text{
      font-size: 14px;
    }

    .list .row{
      display: block !important;
    }

    .list .row-cols-3>*{
      flex: 0 0 auto;
      width: 50%;
      vertical-align:top;
    }

  }
  @media screen and (max-width: 480px) {
    .list .row-cols-3>* {
      width: 100%;
    }
  }

</style>
