<template>
  <div class="main-box">

    <div class="title">
      <v-icon class="btn-back" @click="back">
        fas fa-chevron-left
      </v-icon>
      <div :class="['sub-title']" v-text="$t('option.title')"></div>
    </div>

    <div class="list">
      <div class="themeMode">
        <span style="font-weight: bold; text-align: left; display: flex; padding-left: 10px; flex: 1" v-text="$t('option.themeTitle')"></span>
        <v-icon class="btn-prev" @click="prevTheme">
          fas fa-caret-left
        </v-icon>
        <span style="flex: .8" v-text="mode"></span>
        <v-icon class="btn-next" @click="nextTheme">
          fas fa-caret-right
        </v-icon>
      </div>
      <div class="themeMode">
        <span style="font-weight: bold; text-align: left; display: flex; padding-left: 10px; flex: 1" v-text="$t('option.selectLang')"></span>
        <v-icon class="btn-prev" @click="prevLang">
          fas fa-caret-left
        </v-icon>
        <span style="flex: .8" v-text="selectedLang"></span>
        <v-icon class="btn-next" @click="nextLang">
          fas fa-caret-right
        </v-icon>
      </div>
    </div>
  </div>
</template>

<script>
import i18n from "@/plugins/i18n";

export default {
  name: "Settings",
  beforeMount() {
    const activeLang = localStorage.getItem("user-lang");
    if(activeLang != null){
      this.selectedLang = (this.lang.indexOf(activeLang) == 0 ? "English" : "Indonesia");
      this.selectedLangIndex = this.lang.indexOf(activeLang);
    }

    const activeTheme = localStorage.getItem("user-theme");
    if(activeTheme != null){
      this.mode = this.$t('option.theme.'+activeTheme);
      this.modeIndex = this.listMode.indexOf(activeTheme);
    }else{
      this.mode = this.$t('option.theme.default');
      this.modeIndex = 0;
    }

    this.$parent.$data.showMenu = false;
  },
  data: () => ({
    lang: ['en', 'in'],
    listMode: ['light', 'dark', 'default'],
    selectedLang:"",
    selectedLangIndex:0,
    link:"",
    mode:"",
    modeIndex:0,
    realtimeChek: null
  }),
  methods:{
    nextTheme:function (){
      if(this.modeIndex < 2){
        this.modeIndex++;
      }else{
        this.modeIndex = 0;
      }
      this.mode = this.$t('option.theme.'+this.listMode[this.modeIndex]);
      this.setTheme();
    },
    prevTheme:function (){
      if(this.modeIndex > 0){
        this.modeIndex--;
      }else{
        this.modeIndex = 2;
      }
      this.mode = this.$t('option.theme.'+this.listMode[this.modeIndex]);
      this.setTheme();
    },
    nextLang:function (){
      if(this.selectedLangIndex < 1){
        this.selectedLangIndex++;
      }else{
        this.selectedLangIndex = 0;
      }
      this.selectedLang = (this.selectedLangIndex == 0 ? "English" : "Indonesia");
      this.changeLang();
    },
    prevLang:function (){
      if(this.selectedLangIndex > 0){
        this.selectedLangIndex--;
      }else{
        this.selectedLangIndex = 1;
      }
      this.selectedLang = (this.selectedLangIndex == 0 ? "English" : "Indonesia");
      this.changeLang();
    },
    setTheme(){
      let self = this;
      clearInterval(window.realtimeChek)
      setTimeout(() => {
        localStorage.setItem("user-theme", this.listMode[this.modeIndex]);
        if(this.listMode[this.modeIndex] == "light"){
          document.getElementsByTagName('html')[0].removeAttribute( 'class' );
        }else if(this.listMode[this.modeIndex] == "dark"){
          document.getElementsByTagName('html')[0].setAttribute( 'class', 'theme-dark' );
        }else{
          self.checkTheme();
          window.realtimeChek = setInterval(() => {
            self.checkTheme();
          }, 1000)
        }
      }, 100)
    },
    checkTheme(){
      const hasDarkPreference = window.matchMedia(
          "(prefers-color-scheme: dark)"
      ).matches;

      if(hasDarkPreference){
        document.getElementsByTagName('html')[0].setAttribute( 'class', 'theme-dark' );
      }else{
        document.getElementsByTagName('html')[0].removeAttribute( 'class' );
      }
    },
    changeLang(){
      let self = this;
      setTimeout(() => {
        localStorage.setItem("user-lang", self.lang[self.selectedLangIndex]);
        i18n.locale = self.lang[self.selectedLangIndex];

        self.mode = self.$t('option.theme.'+self.listMode[self.modeIndex]);
      }, 200)
    },
    back: function (){
      this.$router.go(-1);
    },

  }
}
</script>
<style scoped>
  .image-loading{
    width: 100%;
    height: 350px;
  }
  .btn-link{
    display: block;
    width: 400px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border-radius: 20px;
    background-color: var(--ad-blue);
    color: white;
    font-size: 12px;
    font-weight: bold;
    margin: auto;
    margin-top: 10px;
    margin-bottom: 50px;
    text-decoration: none;
  }
  .title {
    margin-top: 0px !important;
    margin-left: 0px !important;
    text-align: left;
    display: flex;
    align-items: center;
  }

  .btn-back{
    padding: 30px 20px;
    cursor: pointer;
    float: left;
    color: var(--ad-text-color);
  }

  .sub-title{
    flex: 1;
    min-height: 25px;
    min-width: 200px;
    font-size: 18px;
    margin: 0px 5px;
    margin-right: 30px;
    display: inline-block;
  }

  .btn-back:hover{
    background: var(--ad-blue);
    color: white;
  }

  .list {
    position: absolute;
    top: 80px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    padding: 0px;
    background-color: var(--ad-blue-grey);
  }

  #carouselExampleIndicators:hover .carousel-control-prev{
    left: 20px;
  }
  #carouselExampleIndicators:hover .carousel-control-next{
    right: 20px;
  }

  .time-mobile{
    text-align: center;
    display: none;
    margin: 20px 0px 0px 0px;
  }

  .text-time.animated-background{
    min-width: 150px;
    height: 25px;
    display: inline-block;
  }

  .text-time{
    font-size: 16px;
    font-weight: bold;
    margin-left: 10px;
    margin-right: 20px;
  }

  .desc{
    text-indent: 50px;
    font-size: 16px;
    text-align: justify;
    margin-top: 20px;
    margin-bottom: 60px;
    margin-left: 30px;
    margin-right: 30px;
    min-height: 80px;
    text-align: justify;

  }

  .list{
    overflow: hidden;
  }

  .themeMode{
    display: flex;
    margin-top: 20px;
    padding-left: 15px;
    padding-right: 15px;
  }

  .themeMode > *{
    flex: 0.1;
    text-align: center;
    color: var(--ad-text-color);
  }

  .themeMode > i{
    cursor: pointer;
  }

  @media screen and (max-width: 960px) {
    .sub-title {
      max-width: 350px;
    }
    .time-mobile{
      display: block;
    }

    .time{
      display: none;
    }

    .head-menu .v-menu__content{
      min-width: initial!important;
    }

    .list .row-cols-3>*{
      flex: 0 0 auto;
      width: 50%;
    }
  }
</style>
