import Vue from 'vue'
import VueRouter from 'vue-router'
import About from "@/views/About"
import Portfolio from "@/views/Portfolio"
import Skills from "@/views/Skills"
import EmailMe from "@/views/EmailMe"
import DetailPortfolio from "@/views/DetailPortfolio"
import Settings from "@/views/Settings"

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'home',
        component: About
    },
    {
        path: '/about-me',
        name: 'about',
        component: About
    },
    {
        path: '/skills',
        name: 'skills',
        component: Skills
    },
    {
        path: '/portfolio',
        name: 'portfolio',
        component: Portfolio
    },
    {
        path: '/email-me',
        name: 'emailMe',
        component: EmailMe
    },
    {
        path: '/detail-portfolio/:id',
        name: 'detailPortfolio',
        component: DetailPortfolio
    },
    {
        path: '/settings',
        name: 'settings',
        component: Settings
    },

];

const router = new VueRouter({
    mode: 'history',
    routes, // short for `routes: routes`
    linkActiveClass: "active",
})



export default router;
