<template>
  <div class="main-box">
    <div class="title" v-text="$t('menu.aboutMe')"></div>
    <div class="line-title"></div>
    <div :class="['desc']" v-html="about.desc">
    </div>
    <button :class="['btn-resume', (loading ? 'animated-background': '')]" @click="$parent.openNewTab(about.resume)">
      <span v-show="!loading" v-text="$t('about.resumeBtn')"></span>
    </button>
  </div>
</template>

<script>
export default {
  name: "About",
  watch: {
    // eslint-disable-next-line no-unused-vars
    '$i18n.locale': function(newVal, oldVal) {
      this.about.desc = `<div class='animated-background' style='height: 20px;width: 95%;float: right; margin-bottom: 10px;'></div>
            <div class='animated-background' style='height: 20px;width: 100%;float: left; margin-bottom: 10px;'></div>
            <div class='animated-background' style='height: 20px;width: 80%;float: left;'></div>`;
      this.loading = true;
      this.getAbout();
    }
  },
  beforeMount() {
    this.getAbout();
    this.$parent.$data.showMenu = true;
  },
  data: () => ({
      about: {
        desc:`<div class='animated-background' style='height: 20px;width: 95%;float: right; margin-bottom: 10px;'></div>
            <div class='animated-background' style='height: 20px;width: 100%;float: left; margin-bottom: 10px;'></div>
            <div class='animated-background' style='height: 20px;width: 80%;float: left;'></div>`
      },
      loading: true
  }),

  methods:{
    getAbout:function (){
      let activeLang = localStorage.getItem("user-lang");
      if(activeLang == null){
        activeLang = "en";
      }
      this.axios.get(`about?lang=${activeLang}&token=${this.$parent.getToken()}`).then((response) => {
        this.loading = false;
        this.about = response.data;
      }).catch((error) =>{
        console.log(error);
      })
    }
  }
}
</script>

<style scoped>
  .main-box{
    background: var(--ad-second);
  }
  .desc{
    text-indent: 20px;
    margin-top: 20px;
    margin-bottom: 60px;
    margin-left: 30px;
    margin-right: 30px;
    min-height: 80px;
    text-align: justify;
    /*background: var(--ad-second);*/
  }

  .btn-resume{
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translate(-50%, 0%);
    width: 400px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border-radius: 20px;
    background-color: var(--ad-blue);
    color: white;
    font-size: 12px;
    font-weight: bold;
  }


  @media screen and (max-width: 960px) {
    .line-title{
      width: 121px;
    }

    .btn-resume {
      width: 80%;
    }
  }


</style>
