<template>
  <div class="main-box">
    <div class="title" v-text="$t('menu.skills')"></div>
    <div class="line-title"></div>
    <div class="list">
      <!--menu-->
      <div class="head-menu">
        <v-menu
            :close-on-content-click="false"
            :nudge-width="200"
            offset-y
            style="display: inline-block;">
          <template v-slot:activator="{ on, attrs }">
            <v-btn
                id="btn-filter"
                style="text-transform: none;"
                color="var(--ad-blue)"
                dark
                v-bind="attrs"
                v-on="on">
              Filter
              <v-icon
                  light
                  right
                  style="font-size: 16px; padding-top: 5px; margin-left: 18px;"
              >
               fas fa-chevron-down
              </v-icon>
            </v-btn>
          </template>
          <v-card>
            <v-list class="p-3">
              <div class="form-check" v-for="(item, index) in category" :key="'filter-'+index">
                <input class="form-check-input" type="checkbox" :value="item" :id="'filter-' + index" v-model="filter" @input="filable()">
                <label class="form-check-label" :for="'filter-' + index">
                  {{item}}
                </label>
              </div>
            </v-list>
            <v-card-actions>
              <v-btn text @click="filter = []; filable();"  style="width: 100%; background: rgb(165 184 203 / 31%);">
                Clear
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-menu>
        <div class="form-group col-xl-4" id="search-box" style="display: inline-block; height: 36px; float: right; position: relative">
          <input type="text" class="form-control" id="search" :placeholder="$t('search')" style="height: 36px; padding-right: 35px" autocomplete="off" v-model="search" @input="filable()">
          <v-icon style="font-size: 16px; position: absolute; right: 12px; top: 12px;">
            fas fa-search
          </v-icon>
        </div>
      </div>
      <!--list item-->
      <div class="row row-cols-3 m-auto" >
        <div class="col" v-show="loading" v-for="i in 3" :key="i">
          <div class="mini-box">
            <div class="animated-background" style=" height: 100px;">
            </div>
            <div class="progress-box">
              <div class="progress-status">
                <div class="progress animated-background">
                  <div class="progress-bar"></div>
                </div>
              </div>
              <span class="load"></span>
            </div>
          </div>
        </div>
        <div class="col item-list" v-for="(skill, index) in skillTemps" :key="'skill-'+index">
          <div class="mini-box" @click="openTab(skill.title)">
            <div class="image animated-background">
              <img :src="skill.image" :alt="skill.title" @load="onImgLoad" @error="onImgError">
            </div>
            <div class="text" v-html="skill.title"></div>
            <div class="progress-box text-center">
              <div :class="['progress-single', (skill.progress > 0 ? 'active':'')]"></div>
              <div :class="['progress-single', (skill.progress > 1 ? 'active':'')]"></div>
              <div :class="['progress-single', (skill.progress > 2 ? 'active':'')]"></div>
              <div :class="['progress-single', (skill.progress > 3 ? 'active':'')]"></div>
              <div :class="['progress-single', (skill.progress > 4 ? 'active':'')]"></div>
              <span class="title-progress" v-text="getStatus(skill.progress)"></span>
            </div>
            <div class="desc">
              <div class="text" v-html="skill.title"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Skills",
  beforeMount() {
    this.getSkills();
    this.$parent.$data.showMenu = true;
  },
  data() {
    return{
      skills: {},
      skillTemps: {},
      loading: true,
      category: {},
      filter: [],
      search: ''
    }
  },
  methods:{
    onImgLoad:function(e) {
      e.path[1].classList.remove("animated-background");
    },
    onImgError:function(e) {
      e.target.src = '/image/no-image.png';
    },
    openTab: function (keyword){
      window.open("https://www.google.com/search?q="+keyword, '_blank');
    },
    getStatus:function (lvl){
      let level = "";
      switch (lvl){
        case 1: level = this.$t('skills.level.beginner'); break;
        case 2: level = this.$t('skills.level.elementary'); break;
        case 3: level = this.$t('skills.level.intermediate'); break;
        case 4: level = this.$t('skills.level.advanced'); break;
        case 5: level = this.$t('skills.level.expert'); break;
        default: level = this.$t('skills.level.unknown');
      }

      return level;
    },
    filable:function (){
      let self = this;
      setTimeout(function (){
        if(self.filter.length > 0){
          self.skillTemps = JSON.parse(JSON.stringify(self.skills)).filter(skill => {
            return self.filter.includes(skill.category) && skill.title.toLowerCase().includes(self.search.toLowerCase());
          });
        }else{
          self.skillTemps = JSON.parse(JSON.stringify(self.skills)).filter(skill => skill.title.toLowerCase().includes(self.search.toLowerCase()));
        }
        setTimeout(function () {
          let list = document.querySelectorAll(".list .item-list");
          list.forEach((v, k) => {
            setTimeout(() => {
              v.classList.add("show")
            }, k * 50);
          })
        },100);
      }, 100);
    },
    getSkills:function (){
      this.axios.get(`skills?token=${this.$parent.getToken()}`).then((response) => {
        this.loading = false;
        this.category = response.data.category;
        this.skills = response.data.data;
        this.skillTemps = response.data.data;
        setTimeout(function (){
          let list = document.querySelectorAll(".list .item-list");
          list.forEach((v, k)=>{
            setTimeout(()=>{
              v.classList.add("show")
            }, k * 100);
          })

        }, 200);

      }).catch((error) =>{
        console.log(error);
      })
    }
  }
}
</script>

<style scoped>
    .mini-box{
      height: 140px;
      width: 100%;
      overflow: hidden;
      background-color: white;
      box-shadow: 0.1px 2px 8px rgba(0, 0, 0, 0.20);
      display: inline-block;
      border-radius: 10px;
      margin: 10px 0px;
      position: relative;
      cursor: pointer;
    }

    .progress-single{
      width: 17%;
      border-radius: 10px;
      margin-right: 5px;
      height: 10px;
      display: inline-block;
      background: var(--ad-dark-blue-grey);
    }

    .title-progress{
      padding: 6px;
      text-transform: uppercase;
      font-size: 12px;
      font-weight: bold;
      background: #055DE1;
      position: absolute;
      top: 0px;
      color: white;
      right: 0px;
    }

    .progress-single.active{
      background: var(--ad-blue);
    }

    .image{
      overflow: hidden;
      height: 100px;
    }

    .image img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    .progress-box{
        width: 100%;
        padding: 7px;
        font-size: 16px;
    }

    .progress{
      height: 10px;
      border-radius: 10px;
    }

    .progress-status{
      display: inline-block;
      width: 79%;
      margin-right: 5px;
    }

    .desc{
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: rgba(0, 0, 0, 0.7);
      opacity: 0;
      text-align: center;
      line-height: 140px;
    }

    .mini-box > .text{
      display: none;
    }

    .text{
      color: white;
      font-weight: bold;
      font-size: 16px;
    }

    .head-menu{
      padding: 0px 18px 18px 18px;
    }

    .mini-box:hover .desc{
      opacity: 1;
    }

    .list{
      overflow-y: auto;
    }

    .list-mobile{
      display: none;
      position: absolute;
      top: 60px;
      bottom: -20px;
      left: 0px;
      right: 0px;
      padding: 20px;
      background-color: var(--ad-blue-grey);
    }

    @media screen and (max-width: 1152px) {
      .progress-status {
        width: 70%;
      }
      .mini-box{
        height: 130px;
      }

      .image{
        height: 90px;
      }
    }

    @media screen and (max-width: 960px) {

      .head-menu .v-menu__content{
        min-width: initial!important;
      }
      #btn-filter{
        width: 100%;
        margin-bottom: 10px;
      }
      #search-box{
        width: 100%!important;
        margin-bottom: 20px;
      }
      .line-title{
        width: 70px;
      }

      .list{
        display: block;
        overflow-y: scroll;
        overflow-x: hidden;
      }

      .text{
        display: none;
      }

      .mini-box > .text{
        display: block;
      }

      .mini-box{
        height: 80px;
        width: 100%;
        overflow: hidden;
        background-color: white;
        box-shadow: 0.1px 2px 8px rgba(0, 0, 0, 0.20);
        display: inline-block;
        border-radius: 10px;
        margin: 10px 0px;
        position: relative;
        cursor: pointer;
      }

      .mini-box .progress-status{
        display: inline-block;
        width: 85%;
        margin-right: 5px;
      }

      .mini-box .text{
        color: black;
        font-weight: bold;
        font-size: 18px;
        margin: 0px 20px;
        margin-top: 10px;
        display: block;
      }

      .mini-box .image{
        display: none;
      }

      .mini-box .desc{
        display: none;
      }

      .list .row{
        display: block !important;
      }

      .list .row-cols-3>*{
        flex: initial !important;
        width: initial !important;
      }
    }

</style>
