<template>
  <div class="main-box">

    <div class="title">
      <v-icon class="btn-back" @click="back">
        fas fa-chevron-left
      </v-icon>
      <div :class="['sub-title', (loading ? 'animated-background': '')]" v-text="title"></div>

      <div class="time">
        <v-icon color="var(--ad-text-color)" size="18" v-show="!loading">
          fas fa-business-time
        </v-icon>
        <span :class="['text-time', (loading ? 'animated-background': '')]" v-text="workingTime"></span>
      </div>
    </div>

    <div class="list">
      <div class="image-loading animated-background" v-show="loading"></div>
      <div id="carouselExampleIndicators" class="carousel slide" data-bs-ride="carousel">
        <div class="carousel-indicators">
          <button type="button" data-bs-target="#carouselExampleIndicators" :data-bs-slide-to="i" :class="[(i == 0 ? 'active' : '')]" v-for="(item,i) in images" :key="i" :aria-label="`Slide` + i "></button>
        </div>
        <div class="carousel-inner">
          <div :class="['carousel-item animated-background', (i == 0 ? 'active' : '')]" v-for="(item,i) in images" :key="'image-'+i" data-bs-interval="6000">
            <img :src="item.link" class="d-block w-100" alt="..." height="350" @load="onImgLoad" @error="onImgError" style="object-fit: cover">
          </div>
        </div>
        <div class="carousel-control-prev" data-bs-target="#carouselExampleIndicators" data-bs-slide="prev">
          <v-icon class="control-prev-icon">
            fas fa-chevron-left
          </v-icon>
        </div>
        <div class="carousel-control-next" data-bs-target="#carouselExampleIndicators" data-bs-slide="next">
          <v-icon class="control-next-icon">
            fas fa-chevron-right
          </v-icon>
        </div>
      </div>
      <div class="time-mobile">
        <v-icon color="var(--ad-text-color)" size="18" v-show="!loading">
          fas fa-business-time
        </v-icon>
        <span :class="['text-time', (loading ? 'animated-background': '')]" v-text="workingTime"></span>
      </div>
      <div :class="['desc', (loading ? 'animated-background': '')]" v-html="desc"></div>

      <button :class="['btn-link', (loading ? 'animated-background': '')]" @click="$parent.openNewTab(link)" v-show="showBtn">
        <span v-show="!loading" v-text="$t('openLink')"></span>
      </button>
    </div>

  </div>
</template>

<script>
export default {
  name: "Detail Portfolio",
  beforeMount() {
    this.getPortfolio();
    this.$parent.$data.showMenu = false;
  },
  data: () => ({
    loading: true,
    showBtn: true,
    title: "",
    desc: "<div style='height: 20px;width: 5%;float: left;background: var(--ad-grey);'></div></div><div style='background: var(--ad-grey);height: 10px; clear: both;'></div><div style='height: 20px'></div><div style='background: var(--ad-grey);height: 10px'></div><div style='height: 20px;width: 20%;float: right;background: var(--ad-grey);'></div>",
    workingTime:"",
    link:"",
    images: []
  }),
  methods:{
    onImgLoad:function(e) {
      e.path[1].classList.remove("animated-background");
    },
    onImgError:function(e) {
      e.target.src = '/image/no-image-slide.png';
    },
    back: function (){
      this.$router.push('/portfolio');
    },
    getPortfolio:function (){
      let activeLang = localStorage.getItem("user-lang");
      if(activeLang == null){
        activeLang = "en";
      }
      this.axios.get(`portfolio/${this.$route.params.id}?lang=${activeLang}&token=${this.$parent.getToken()}`).then((response) => {
        this.loading = false;
        let data = response.data.data;
        this.title = data.title;
        this.desc = data.description;
        this.workingTime = data.working_time;
        this.images = response.data.links;
        this.link = data.link;
        if(this.isEmpty(data.link)){
          this.showBtn = false;
        }
      }).catch((error) =>{
        console.log(error);
      })
    },
    isEmpty:function (data){
        return (data == 'undefined' || data == undefined || data == null || data == "" || data == " ");
    }

  }
}
</script>
<style scoped>
  .image-loading{
    width: 100%;
    height: 350px;
  }
  .btn-link{
    display: block;
    width: 400px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border-radius: 20px;
    background-color: var(--ad-blue);
    color: white;
    font-size: 12px;
    font-weight: bold;
    margin: auto;
    margin-top: 10px;
    margin-bottom: 50px;
    text-decoration: none;
  }
  .title {
    margin-top: 0px !important;
    margin-left: 0px !important;
    text-align: left;
    display: flex;
    align-items: center;
  }

  .btn-back{
    padding: 30px 20px;
    cursor: pointer;
    float: left;
    color: var(--ad-text-color);
  }

  .btn-back::after{
  }

  .sub-title{
    flex: 1;
    min-height: 25px;
    min-width: 200px;
    font-size: 18px;
    margin: 0px 5px;
    margin-right: 30px;
    display: inline-block;
  }

  .btn-back:hover{
    background: var(--ad-blue);
    color: white;
  }

  .list {
    position: absolute;
    top: 80px;
    bottom: -40px;
    left: 0px;
    right: 0px;
    padding: 0px;
    background-color: var(--ad-grey);
  }
  .control-next-icon, .control-prev-icon{
    width: 30px;
    height: 30px;
    margin:0px;
    color: #000000;
    font-size: 24px;
  }

  .carousel-control-prev, .carousel-control-next{
    background-color: #ffffff;
    opacity: 1;
    top: 50%;
    bottom: initial;
    width: auto;
    padding: 5px;
    border-radius: 50%;
    transition: .2s;
    transform: translate(0, -50%);
    display: inline-block;
    cursor: pointer;;
  }

  .carousel-control-prev{
    left: -50px;
  }

  .carousel-control-next{
    right: -50px;
  }

  #carouselExampleIndicators:hover .carousel-control-prev{
    left: 20px;
  }
  #carouselExampleIndicators:hover .carousel-control-next{
    right: 20px;
  }

  .time-mobile{
    text-align: center;
    display: none;
    margin: 20px 0px 0px 0px;
  }

  .text-time.animated-background{
    min-width: 150px;
    height: 25px;
    display: inline-block;
  }

  .text-time{
    font-size: 16px;
    font-weight: bold;
    margin-left: 10px;
    margin-right: 20px;
  }

  .desc{
    text-indent: 50px;
    font-size: 16px;
    text-align: justify;
    margin-top: 20px;
    margin-bottom: 60px;
    margin-left: 30px;
    margin-right: 30px;
    min-height: 80px;
    text-align: justify;

  }

  .list{
    overflow-y: scroll;
    overflow-x: hidden;
  }

  @media screen and (max-width: 960px) {
    .sub-title {
      max-width: 350px;
    }
    .time-mobile{
      display: block;
    }

    .time{
      display: none;
    }

    .head-menu .v-menu__content{
      min-width: initial!important;
    }

    .list .row-cols-3>*{
      flex: 0 0 auto;
      width: 50%;
    }



  }
</style>
